@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  letter-spacing: 1px;
 }
body {
  font-family:'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
p{
  font-size: 1.6rem;
}
h1{
  font-size: 6rem;
}
.bold{
  font-weight: 700;
}
.error{
  color: rgba(255, 0, 0,0.8);
  background-color: rgba(255, 255, 255,0.5);
}
.app{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}
.app:before{
  content: '';
  position: absolute;
  background: url('./pexels-pixabay-531756.jpg') no-repeat center center/cover;
  background: url('./pexels-guru-muxamil-15655926.jpg') no-repeat center center/cover;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}
.heading{
  text-align: center;
  font-size: 7rem;
}

/* form styling */

.app .search{
  text-align: center;
  padding: 1rem;
}
.app input{
  padding: .7rem 1.5rem;
  font-size: 2.5rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background-color: rgba(0, 0, 0, 0.2);
  color: white;
  width:60vw;
  height:100%;
  text-align: center;
}
.app input:focus{
  outline: none;
}
::placeholder{
  color: white;
  text-align: center;
}
.search form{
  position: relative;
}
.search label{
  position: absolute;
  left:-99999px
}

/* output container styling */

.container{
  width: 60vw;
  height: 50vh;
  margin: 1rem auto;
  padding: 0 1rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
}

/* container top styling */

.app .top{
  width: 100%;
  margin: 1rem auto;
  display:flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
}
.location{
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}
.location p{
  font-size: 4rem;
}
.description{
  display: flex;
  flex-flow: row  wrap;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}
.description .img img{
  width: 200px;
  height: 200px;
  image-rendering: optimizeQuality;
}
.description p{
  font-size: 3rem;
}


/* container bottom styling */

.app .bottom{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2);
  gap: 2rem;
}

/* RESPONSIVE SCREEN STYLING  */

@media (width<=1200px){
  p{
    font-size: 1.5rem;
  }
  h1{
    font-size: 5rem;
  }
  .heading{
    text-align: center;
    font-size: 6rem;
  }
  .app input{
    font-size: 2.3rem;
  }
  .location p{
    font-size: 3.5rem;
  }
  .description .img img{
    width: 180px;
    height: 180px;
  }
  .description p{
    font-size: 2.5rem;
  }
}
@media (width<=992px){
  p{
    font-size: 1.4rem;
  }
  h1{
    font-size: 4rem;
  }
  .heading{
    text-align: center;
    font-size: 5rem;
  }
  .app input{
    font-size: 2.1rem;
  }
  .location p{
    font-size: 3rem;
  }
  .description .img img{
    width: 160px;
    height: 160px;
  }
  .description p{
    font-size: 2rem;
  }
}
@media (width<=768px){
  p{
    font-size: 1.3rem;
  }
  h1{
    font-size: 3.5rem;
  }
  .heading{
    text-align: center;
    font-size: 4.5rem;
  }
  .app input{
    font-size: 2rem;
  }
  .location p{
    font-size: 2.5rem;
  }
  .description .img img{
    width: 150px;
    height: 150px;
  }
  .description p{
    font-size: 1.8rem;
  }
}
@media (width<=576px){
  p{
    font-size: 1rem;
  }
  h1{
    font-size: 3.5rem;
  }
  .heading{
    text-align: center;
    font-size: 4rem;
  }
  .app input{
    font-size: 1.8rem;
  }
  .location p{
    font-size: 1.8rem;
  }
  .description .img img{
    width: 130px;
    height: 130px;
  }
  .description p{
    font-size: 1.8rem;
  }
}